@import url(https://fonts.googleapis.com/css2?family=Baloo+Thambi+2&family=Comic+Neue:wght@300&display=swap);
/*Main Dashboard css*/
.dashboard-body {
  height: 100vh;
  margin-top: 60px;
}

.main-content {
  height: 100vh;
}

/*GREETINGS*/
.IntroBoxInner {
  border-radius: 10px;
}

.Greetings {
  display: inline-block;
  min-width: 100%;
}

/*Title*/
.titles {
  display: inline-flex;
  width: 100%;
  max-height: 2rem;
}

.SubjectTitle {
  float: left;
  width: 50%;
  font-size: 19px;
}

.LinkShow {
  float: right;
  right: 0;
  width: 50%;
  font-size: 15px;
}

/*PROJECTS PAGE*/
.projects-section {
  height: 50%;
}

.inner-elements-projects-section {}

.projects-viewer-wrapper {
  display: block;
  margin: auto;
}

.add-projects-button {

  border-radius: 8px;
}

.add-projects-button * {
  padding: 9px;
}

.add-project-button-text {
  font-size: 1.2vw;

}

.project-card {
  height: 40vh;
}

/*EXAM CARDS*/
.cardCustom {
  box-shadow: none;
}

.FirstSubject {
  color: white;
}

.innerCardExams {
  display: inline;
  width: 100%;
}

.TextsInnerCardExams {
  position: relative;
  top: 1vh;
}

.SubjectTittle {
  float: left;
  width: 31%;
}

.DayofWeek {
  float: left;
  width: 14%;
}

.Professor {
  float: left;
  width: 25%;
}

.timeOfExam {
  float: right;
  width: 20%;
  padding: 0;
}

.MenuTogler {
  float: right;
  width: 10%;
}

/*ASSIGNMENTS AND TODOS*/

.assignmentCard {
  box-shadow: 0.1rem 0.12rem 0.11rem 0.11rem rgba(0, 0, 0, 0.075);
  border-radius: 10px;
  min-width: 100%;
  padding: 7px;
}

.assignmentCard:hover {
  box-shadow: 0 0 11px rgba(33, 33, 33, 0.2);
}

.innerCard {
  display: inline-block;
  min-width: 100%;
}

.Indicator {
  width: 3%;
  font-size: 0.5rem;
  position: relative;
  top: 1vh;
  float: left;
}

.UndoneIndicator {
  color: #f8c722;
}

.DoneIndicator {
  color: #8db98d;
}

.TodoSubject {
  width: 24%;
  float: left;
}

.TodoTittle {
  width: 45%;
  float: left;
}

.timeOfExam {
  width: 15%;
  float: left;
}

.State {
  width: 13%;
  float: right;
  font-size: 0.95rem;
  position: relative;
  top: 0.6vh;
}

.Undone {}

.Done {
  color: #8db98d;
  font-weight: 500;
}

.customButton {
  min-width: 100%;
  border-radius: 20px;
  transition: all 1s;
  border: 1px solid #f8c722;
  color: #f8c722;
  background: white;
  padding: 5px;
}

.FinishedButton {
  border: none;
  font-size: 1rem;
  color: #8db98d;
}

.customButton:hover {
  transition: all 1s;
  background: linear-gradient(90deg,
      rgba(249, 181, 27, 1) 35%,
      rgba(247, 208, 80, 1) 87%);
  color: white;
}

.below {
  font-size: 11px;
}

.assignmentCard.highlighted {
  color: white;
}

.assignmentCard.highlighted .below {
  color: white;
}

.assignmentCard.highlighted .customButton {
  background: linear-gradient(90deg,
      rgba(249, 181, 27, 1) 35%,
      rgba(247, 208, 80, 1) 87%);

  color: white;
}

/*SUBJECTS*/
.innerSubjects {
  padding-top: 3px;
}

.fir-image-figure {
  display: flex;
  align-items: center;
  margin-bottom: 12.5px;
  position: relative;
  text-decoration: none;
}

.SubjectCardTittle {
  min-width: 75%;
  max-width: 75%;
}

.SubjectImage {
  min-width: 15%;
  max-width: 15%;
}

.togleMenuSubjects {
  min-width: 10%;
  max-width: 10%;
}

.toggleMenu {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: black;
  box-shadow: 0px 40px 0px black, 0px 80px 0px black;
}

.fir-clickcircle {
  height: 3rem;
  width: 3rem;
  border-radius: 100px;
  cursor: pointer;
}

.fir-image-figure .caption,
.fir-image-figure figcaption {
  padding-left: 15px;
}

figcaption {
  max-width: 90%;
  min-width: 90%;
}

.fig-author-figure-title {
  font-size: 12px;
  position: relative;
  top: -5px;
}

.dropdownTogggle {
  position: relative;
  right: 10px;
}

/*SMAL CALENDAR*/
.small-calendar {
  padding: 20px;
}

.calendar-day {
  min-width: 100%;
  border-bottom: none;
  cursor: pointer;
  padding: 4px;
}

.calendar-day-number.today {
  min-height: 1.7rem;
  max-width: 2.1rem;
  color: black;
  padding: 4px;
  height: 100%;
  background-image: linear-gradient(to right top,
      #9b9efc,
      #8e92fd,
      #8186fe,
      #7279ff,
      #636dff);
  border-radius: 50%;
  color: white;
}

@media only screen and (max-width: 575px) {
  .State {
    display: none;
    width: 0;
  }

  .TodoTittle {
    width: 55%;
  }

  .TodoSubject {
    width: 25%;
  }

  .timeOfExam {
    width: 15%;
  }
}

@media only screen and (min-width: 575px) and (max-width: 888px) {
  .small-calendar {
    padding: 0;
    border-top: none;
  }

  .calendar-day-number.today {
    padding: 2px;
  }
}

@media only screen and (max-width: 991px) {
  .small-calendar {
    margin-top: 15px;
  }

  .calendar-day-number.today {
    max-height: 30px;
  }
}
/*Fonts*/
.modern {
  font-family: "Baloo Thambi 2", cursive;
}

/*FONT WEIGHT*/
.bold {
  font-weight: 700;

}

/*FONT-SIZES*/
.titleFont {
  font-size: 1rem;
}

.afterFont {}

.descriptionFont {}

.notice {
  font-size: 0.2rem;
}

/*Colors*/
.greyFont {
  color: #747373;
}

.whiteFont {
  color: white;
}

/*Backgrounds*/
.primaryTheme {
  background-image: linear-gradient(109.6deg, rgba(125, 89, 252, 0.8) 11.2%, #7279ff 91.1%);
}

.secondary {
  background: linear-gradient(90deg,
      rgba(249, 181, 27, 1) 35%,
      rgba(247, 208, 80, 1) 87%);
}

/*Functions and Icons*/
.closingIcon {
  cursor: pointer;
}

/*Dropdown none */

.dropleft .dropdown-toggle::before {
  display: none;
}

.dropdown-toggle:after {
  display: none;
}
.authentication {
    background-color: #a06df2;
    min-width: 100%;
    min-height: 100vh;

}

.background-holder {
    background: url("/media/authentication/authenticationBG.svg");
    width: 100vw;
    height: 100vh;
    background-repeat: no-repeat;
    background-position: center;
    display: grid;
}

.error-message {
    color: lightcoral;
}

/*FORM*/
.form-holder {
    margin: auto;
    width: 27rem;
    padding: 2.5rem;
    border-radius: 10px;
}

.logo {
    width: 10rem;
    margin-bottom: 1rem;
}

.inputBox {
    padding-left: 12px;
    padding-right: 13px;
}

.form-items h3 {
    margin-bottom: 0.5rem;
    font-weight: 500;
    line-height: 1.2;
    font-size: 1.5rem;
    color: #343a40;
}

.form-items p {
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
}

.form-content .form-control,
.form-content .dropdown-toggle.btn-default {
    width: 100%;
    padding: 24px 20px;
    text-align: left;
    border: 0;
    outline: 0;
    background-color: #f7f7f7;
    border: 1px solid #f7f7f7;
    border-radius: 6px;
    font-size: 15px;
    font-weight: 300;
    color: #8d8d8d;
    transition: all 0.3s ease;
    margin-bottom: 14px;
}

.form-content input.passNoMatch {
    border: 1px solid red;
}

.forgotAndRemember {
    width: 100%;
}

.buttonSubmit {
    padding: 0.7rem;
    width: 100%;
    border: none;
    border-radius: 10px;
    background-color: #a06df2;
    color: white;
    font-weight: 700;
    word-spacing: 10%;
    z-index: 1;
}

.custom-control-register {
    padding-top: 0.8rem;
}

.registerBut {
    width: 50%;
}

hr::after {
    content: "OR";
    position: relative;
    top: -12px;
    z-index: 4;
    color: grey;
    padding: 0 0.25em;
    background: white;
}

.firebaseui-idp-google {
    background: white;
    padding: 10px;
    width: 100%;
    color: black;
    font-weight: 500;
}

.firebaseui-idp-google {
    background: white;
    border: none;
}

.firebaseui-idp-icon {
    width: 25px;
}

@media only screen and (max-width: 575px) {
    .authentication {
        background: white;
    }

    .form-holder {
        width: 100%;
        height: 100%;
    }
}
.navi {
    box-shadow: none;
    padding-left: 6%;
    padding-right: 6%;
    max-height: 60px;
    background-color: #fff;
}

.naviLinks {
    margin-left: 25%;
    margin-right: 25%;
}

.NavbarLink {
    color: #747373;
    font-size: 16px;

}

.NavbarLink:hover {
    color: #7279ff;
    transition: 1s;
}

.activeLink {
    background-image: linear-gradient(to right top, #9b9efc, #8e92fd, #8186fe, #7279ff, #636dff);
    border-radius: 10px;
    color: white;

}

.activeLink:hover {
    color: white;
}


@media only screen and (max-width: 991px) {
    .avatar img {
        display: none;
    }

    .naviLinks {
        margin-left: 0;
        background-color: #fff;

        margin-right: 0;
        padding-top: 10px;
    }
}
